.flex_projects {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.project {
  background-color: #f1f1f1;
  /*width: 200px;*/
  flex-grow: 1;
  height: 40vh;
  /*height: 250px;*/
  margin: 25px;
/*  height: 500px;
  width: 250px;*/
  /*padding: 50px;*/

  /*padding: 20px;*/
/*  margin: auto;
  padding: 20px;
  width: 500px;*/
  /*overflow: hidden;*/
  position: relative;
}

.project-image {
  /*display: block;*/
  max-height: 100%;
  min-width: 100%;
  object-fit: cover;
  vertical-align: bottom;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .3s ease;
  background-color: #008CBA;
}

.project:hover .overlay {
  opacity: 0.9;
}

.project-title {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  opacity: 1;
}

.project-category {
  display: none;
}

